.title {
  font-weight: 600;
  font-size: 20px;
  line-height: 26px;
  color: #333333;
}

.wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 50px;
}

.container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  border-radius: 12px;
}

.button {
  align-self: flex-end;
  margin-top: 50px;
}
